body, html {
  margin: 0;
  padding: 0;
  font-family: "Arial", sans-serif;
  background-color: #f0f0f0;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: background-color 0.3s ease;
}

body.dark-mode {
  background-color: #1a1a1a;
  color: #ddd;
}

html {
  background-color: inherit;
  min-height: 100%;
  transition: background-color 0.3s ease;
}

.status-page {
  width: 900px;
  padding: 20px;
  background-color: #fff;
  border-radius: 15px;
  transition: box-shadow 0.3s ease, background-color 0.3s ease, color 0.3s ease;
}

.status-page.operational {
  box-shadow: 0px 0px 100px rgba(0, 180, 0, 0.4);
}

.status-page.partial {
  box-shadow: 0px 0px 100px rgba(234, 196, 82, 0.4);
}

.status-page.down {
  box-shadow: 0px 0px 100px rgba(255, 0, 0, 0.4);
}

body.dark-mode .status-page {
  background-color: #2a2a2a;
}

.status-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-bottom: 20px;
  border-bottom: 2px solid #f0f0f0;
  transition: border-color 0.3s ease;
}

body.dark-mode .status-header {
  border-bottom: 2px solid #3a3a3a;
}

.status-header-left {
  display: flex;
  flex-direction: column;
}

.status-header-left h1 {
  font-size: 32px;
  margin: 0;
  color: #000;
  transition: color 0.3s ease;
}

body.dark-mode .status-header-left h1 {
  color: #ddd;
}

.status-header-left p {
  margin: 0;
  margin-top: 5px;
  color: #777;
  transition: color 0.3s ease;
}

body.dark-mode .status-header-left p {
  color: #bbb;
}

.status-header-right {
  display: flex;
  align-items: baseline;
}

.status-header-right .menu-icon img {
  height: 14px;
  width: auto;
  margin-right: 10px;
}

.menu-icon.disabled {
  opacity: 0.5;
  pointer-events: none;
}

.menu-icon.disabled img {
  cursor: not-allowed;
}

.status-header-right h2 {
  font-size: 20px;
  margin: 0;
  color: #000;
  transition: color 0.3s ease;
}

body.dark-mode .status-header-right h2 {
  color: #ddd;
}

.status-text {
  font-size: 14px;
  margin-top: 5px;
  color: #777;
  display: flex;
  align-items: center;
  justify-content: flex-end;
  align-self: flex-end;
  transition: color 0.3s ease;
}

body.dark-mode .status-text {
  color: #bbb;
}

.status-text img {
  width: 16px;
  height: auto;
  margin-right: 5px;
}

.status-section {
  background-color: #fff;
  padding: 15px;
  border-radius: 15px;
  margin-top: 20px;
  transition: background-color 0.3s ease;
}

body.dark-mode .status-section {
  background-color: #2a2a2a;
}

.status-item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 15px;
  background-color: #f0f0f0;
  padding: 15px;
  border-radius: 10px;
  cursor: pointer;
  transition: background-color 0.2s ease;
}

body.dark-mode .status-item {
  background-color: #3a3a3a;
}

.status-item:hover {
  background-color: #e6e6e6;
}

body.dark-mode .status-item:hover {
  background-color: #444;
}

.status-info {
  display: flex;
  align-items: center;
}

.status-info img {
  width: 24px;
  height: 24px;
  margin-right: 10px;
}

.status-info h3 {
  margin: 0;
  font-size: 18px;
  color: #000;
  transition: color 0.3s ease;
}

body.dark-mode .status-info h3 {
  color: #ddd;
}

.status-info p {
  margin: 0;
  color: #777;
  transition: color 0.3s ease;
}

body.dark-mode .status-info p {
  color: #bbb;
}

.status-bars {
  text-align: right;
}

.uptime-bar {
  background-color: #e0e0e0;
  height: 8px;
  border-radius: 4px;
  width: 180px;
  position: relative;
  margin-bottom: 5px;
}

body.dark-mode .uptime-bar {
  background-color: #4a4a4a;
}

.uptime-bar span {
  display: block;
  height: 100%;
  background-color: #00b400;
  border-radius: 4px;
  position: absolute;
  top: 0;
  left: 0;
}

.status-bars p {
  margin: 0;
  font-size: 12px;
  color: #777;
  transition: color 0.3s ease;
}

body.dark-mode .status-bars p {
  color: #bbb;
}

.service-details {
  margin-top: 10px;
  padding: 15px;
  background-color: #f9f9f9;
  border-radius: 10px;
  display: flex;
  flex-direction: column;
  transition: background-color 0.3s ease;
}

body.dark-mode .service-details {
  background-color: #3a3a3a;
}

.detail-item {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.detail-item img {
  width: 20px;
  height: auto;
  margin-right: 15px;
  margin-left: 15px;
}

.detail-item p {
  text-align: left;
  flex-grow: 1;
  color: #000;
  transition: color 0.3s ease;
}

body.dark-mode .detail-item p {
  color: #bbb;
}

.detail-status {
  display: flex;
  align-items: center;
}

.service-details {
  max-height: 0;
  overflow: hidden;
  transition: max-height 0.3s ease, margin 0.3s ease;
  margin-bottom: 0;
  padding: 0;
}

.service-details.expanded {
  max-height: 200px;
  padding: 0px;
  background-color: #f9f9f9;
  border-radius: 10px;
  margin-top: 10px;
  margin-bottom: 20px;
}

body.dark-mode .service-details.expanded {
  background-color: #3a3a3a;
}

.status-footer {
  text-align: center;
  margin-top: 20px;
  font-size: 12px;
  color: #aaa;
  transition: color 0.3s ease;
}

body.dark-mode .status-footer {
  color: #888;
}

.skeleton {
  background-color: #e0e0e0;
  border-radius: 4px;
  display: inline-block;
  position: relative;
  overflow: hidden;
}

.skeleton::before {
  content: "";
  display: block;
  position: absolute;
  left: -150px;
  top: 0;
  height: 100%;
  width: 150px;
  background: linear-gradient(90deg, rgba(255, 255, 255, 0), rgba(255, 255, 255, 0.6), rgba(255, 255, 255, 0));
  animation: skeleton-loading 1.5s infinite;
}

@keyframes skeleton-loading {
  0% {
    left: -150px;
  }
  100% {
    left: 100%;
  }
}

.skeleton-service-item {
  display: flex;
  justify-content: start;
  align-items: center;
  margin-bottom: 15px;
  background-color: #f0f0f0;
  padding: 15px;
  border-radius: 10px;
  height: 40px;
}

body.dark-mode .skeleton-service-item {
  background-color: #3a3a3a;
}

.skeleton-service-item .skeleton-icon {
  width: 32px;
  height: 32px;
  border-radius: 50%;
}

.skeleton-service-item .skeleton-text {
  height: 16px;
  width: 120px;
  margin-left: 10px;
  margin-bottom: 20px;
  background-color: #e0e0e0;
}

body.dark-mode .skeleton-service-item .skeleton-text {
  background-color: #4a4a4a;
}

.skeleton-service-item .skeleton-text-details {
  height: 16px;
  width: 60px;
  margin-left: -120px;
  margin-bottom: -20px;
  background-color: #e0e0e0;
}

body.dark-mode .skeleton-service-item .skeleton-text-details {
  background-color: #4a4a4a;
}

.skeleton-service-item .skeleton-bars {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  margin-left: auto;
}

.skeleton-service-item .skeleton-bar {
  width: 150px;
  height: 8px;
  margin-bottom: 5px;
  background-color: #d0d0d0;
  border-radius: 4px;
}

body.dark-mode .skeleton-service-item .skeleton-bar {
  background-color: #5a5a5a;
}

.skeleton-service-item .skeleton-uptime {
  width: 60px;
  height: 14px;
  background-color: #d0d0d0;
  border-radius: 4px;
}

body.dark-mode .skeleton-service-item .skeleton-uptime {
  background-color: #5a5a5a;
}

.fade-enter {
  opacity: 0;
}

.fade-enter-active {
  opacity: 1;
  transition: opacity 0.5s ease-in;
}

.fade-exit {
  opacity: 1;
}

.fade-exit-active {
  opacity: 0;
  transition: opacity 0.5s ease-out;
}

.github-link {
  position: absolute;
  top: 20px;
  left: 20px;
  width: 40px;
  height: 40px;
  background-color: #fff;
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}

body.dark-mode .github-link {
  background-color: #3a3a3a;
}

.github-link img {
  width: 24px;
  height: 24px;
}

.dark-mode-toggle {
  position: absolute;
  top: 20px;
  right: 20px;
  width: 40px;
  height: 40px;
  background-color: #fff;
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  cursor: pointer;
}

body.dark-mode .dark-mode-toggle {
  background-color: #3a3a3a;
}

.dark-mode-toggle img {
  width: 24px;
  height: 24px;
}

body.dark-mode .invert {
  filter: invert(1);
}